$prefix: '.wc-booster';

#{$prefix}-wrapper {
    #{$prefix}-wishlist-button {
		padding: 0;
		background: transparent;
		border: none;
		display: flex;
		gap: 10px;
		font-size: 18px;

		i.fa{
			color: #cbc2c2;
		}

		i.fa.added{
			color: #e53737;
		}
	}
}

#{$prefix}-wishlist-page {
    // max-width: 920px;
    margin: 50px auto;
    padding: 15px;
	#{$prefix}-wishlist-title {
		text-align: center;
		font-size: 32px;
		margin-bottom: 40px;
	}
	table {
		width: 100%;
		border-collapse: collapse;
		tr {
			border-top: 1px solid #b9b9b9;
		}

		tbody {
			tr {
				border-bottom: 1px solid #b9b9b9;
			}
		}

		th {
			 border-right: 1px solid #b9b9b9;
			 &:last-child {
				border-right: none;
			 }
		}

		td, th {
			// border-right: 1px solid #b9b9b9;
			padding: 15px;
			text-align: center;
		}

		.quantity {
			width: 70px;
			margin: auto;
			input {
				background-color: #f7f7f7;
				width: 70px;
				padding: 10px;
			}

			button {
				display: none;
			}
		}
		button.product-remove-btn {
			background: #f7f7f7;
			border: none;
			font-size: 20px;
			border-radius: 50%;
			padding: 7px;
			height: 40px;
			width: 40px;
			cursor: pointer;
			transition: all .3s;
			&:hover {
				background-color: #000;
				color: #fff;
			}
		}
 
		td.product-title-img a {
			display: flex;
			align-items: center;
			color: #000;

			img {
				padding: 10px;
			}
		}

		td.add-to-cart-btn{
			display: flex;
		    gap: 10px;
		    align-items: center;
			justify-content: center;
		}
	}
	
}

#{$prefix}-empty-wishlist {
    text-align: center;
    font-size: 21px;
}

// layout grid 
.wc-booster-wishlist-grid {
	.wc-booster-product-block {
		display: flex;
		// flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		padding: 15px;
		border-radius: 6px;
		&:nth-child(odd) {
			background-color: #eee;
		}

		.product-img {
			height: 50px;

			img {
				height: 50px;
    			width: 50px;
				object-fit: cover;
				border-radius: 6px;
			}
		}

		.product-contents {
			max-width: 50%;
    		flex: 0 0 50%;

			a {
				text-decoration: none;
				font-size: 18px;
				display: block;
				color: #000;
			}

			.product-price {
				color: #4e4e4e;
				font-weight: 500;
				display: block;
			}
			
		}
	}
}